<template>
  <el-container class="container">
    <el-aside :width="`${asideWidth}px`"
              class="aside hidden-xs-only">
      <my-menu v-on:test="getAsideWidth"></my-menu>
    </el-aside>
    <el-container class="main">
      <el-main style="padding:0;width:100%;transition:all .5s">
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import MyMenu from './Menu.vue'
export default {
  components: {
    MyMenu
  },
  data () {
    return {
      asideWidth: 200
    }
  },
  methods: {
    getAsideWidth (e) {
      this.asideWidth = e;
    }
  }
}
</script>

<style scoped lang="scss">
.container {
  display: flex;
  .aside {
    height: 100vh;
  }
  .main {
    transition: all 0.5s;
    padding: 30px;
  }
  @media screen and (max-width: 768px) {
    .main {
      padding: 0;
    }
  }
}
.el-container .el-main {
  color: #333;
  padding: 30px;
  overflow: visible;
}
body > .el-container {
  margin-bottom: 40px;
}
</style>



