<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="4"
              v-for="(movie,idx) in movies"
              :key="idx">
        <el-card class="movie"
                 shadow="hover"
                 :body-style="{ padding: '0px' }">
          <img :src="movie.data.images.small"
               class="image">
          <div style="padding: 14px;">
            <span class="title">{{movie.data.title}}</span>
            <div class="bottom">
              <span class="actors">
                <span v-for="(actor,index) in movie.data.casts"
                      :key="index">
                  {{actor.name}}
                </span>
              </span>
              <el-popover placement="bottom-end"
                          trigger="click">
                <div>{{downloadTitle(movie.type)}} {{movie.downloadUrl}}
                  <i v-clipboard:copy="movie.downloadUrl"
                     v-clipboard:success="onCopy"
                     v-clipboard:error="onError"
                     class="el-icon-document"
                     style="cursor:pointer"></i>
                </div>
                <el-button type="text"
                           slot="reference">查看下载链接</el-button>
              </el-popover>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      movies: []
    }
  },
  mounted () {
    this.axios.getMovies()
      .then(res => {
        console.log(res)
        this.parse(res.data)
      })
      .catch(err => console.error(err))
  },
  methods: {
    parse (data) {
      data.forEach(movie => {
        this.movies.push({
          id: movie.id,
          downloadUrl: movie.downloadUrl,
          type: movie.type,
          data: JSON.parse(movie.data)
        })
      })
    },
    onCopy (e) {
      this.$message({
        message: '复制成功',
        type: 'success'
      })
    },
    onError (e) {
      this.$message.error('复制失败')
    },
    downloadTitle (type) {
      switch (type) {
        case 'thunder': return '迅雷链接:';
        case 'torrent': return '种子下载:';
        default: return '磁力链接:'
      }
    }
  }
}
</script>

<style scpoed lang="scss">
.movie {
  .image {
    width: 100%;
  }
  .title {
    font-size: 16px;
  }
  .actors {
    font-size: 12px;
    color: gray;
  }
}
</style>



