import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index'
import Home from '@/components/Home'
import NewArticle from '@/components/backend/NewArticle'
import ArticleList from '@/components/Article/ArticleList'
import Article from '@/components/Article/Article'
import BackendHome from '@/components/backend/BackendHome'
import EditArticle from '@/components/backend/EditArticle'
import Login from '@/components/user/Login'
import AddMovie from '@/components/backend/AddMovie/AddMovie'
import MovieList from '@/components/movie/MovieList'
Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'Home',
    component: Home,
    children: [{
        path: '/',
        name: 'ArticleList',
        component: ArticleList
      },
      {
        path: '/movie',
        name: 'MovieList',
        component: MovieList
      },
      {
        path: '/article/:id',
        component: Article
      },
      {
        path: '/backend',
        name: 'backend',
        component: BackendHome,
        meta: {
          requiresAuth: true
        }
      },
      {
        path: '/backend/newArticle',
        name: 'newArticle',
        component: NewArticle,
        meta: {
          requiresAuth: true
        }
      },
      {
        path: '/backend/addMovie',
        name: 'addMovie',
        component: AddMovie,
        meta: {
          requiresAuth: true
        }
      },
      {
        path: '/backend/editArticle/:id',
        component: EditArticle,
        meta: {
          requiresAuth: true
        }
      },
      {
        path: '/user/login',
        component: Login
      }
    ]
  }, ]
})

router.beforeEach((to, from, next) => {
  console.log(store.getters.isLogin)
  if (to.meta.requiresAuth) {
    if (store.getters.isLogin) {
      next();
    } else {
      next({
        path: '/user/login',
        query: {
          redirect: to.fullPath
        }
      })
    }
  } else {
    next();
  }
})

export default router



