import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    token: JSON.parse(window.sessionStorage.getItem('token')),
  },
  getters: {
    isLogin: state => {
      console.log(state)
      if (state.token) {
        const {
          token,
          expiresIn
        } = state.token
        const hasToken = typeof token !== 'undefined'
        const expired = new Date() > (expiresIn * 1000)
        return hasToken && !expired
      } else return false

    }
  },
  mutations: {
    LOGIN: (state, data) => {
      state.token = data;
      window.sessionStorage.setItem('token', JSON.stringify(data))
    },
    LOGOUT: (state) => {
      state.token = null;
      window.sessionStorage.removeItem('token')
    }
  },
  actions: {
    UserLogin({
      commit
    }, data) {
      commit('LOGIN', data)
    },
    UserLogout({
      commit
    }) {
      commit('LOGOUT')
    }
  }
})

export default store



