<template>
  <i class="back-to-top el-icon-caret-top"
     @click="toTop" />
</template>

<script>
export default {
  methods: {
    toTop () {
      document.documentElement.scrollTop = document.body.scrollTop = 0
    }
  }
}
</script>

<style scoped lang="scss">
.back-to-top {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  box-shadow: 0 0 5px gray;
  position: fixed;
  bottom: 50px;
  right: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
  background: #fff;
  z-index: 999;
  cursor: pointer;
  color: gray;
}
</style>



