<template>
  <el-form ref="form"
           :model="form"
           :rules="rules"
           label-width="80px"
           class="form">
    <el-card class="movie-card"
             v-if="hasMovie"
             :body-style="{ padding: '0px' }">
      <img :src="form.movie.images.small"
           class="image">
      <div style="padding: 14px;">
        <span class="title">{{form.movie.title}}</span>
        <div class="actors">
          <span class="actors">
            <span v-for="(actor,index) in form.movie.casts"
                  :key="index">
              {{actor.name}}
            </span>
          </span>
        </div>
      </div>
    </el-card>
    <el-form-item label="搜索电影"
                  prop="name">
      <el-autocomplete v-model="form.name"
                       :fetch-suggestions="querySearchAsync"
                       placeholder="请输入电影名"
                       :trigger-on-focus="false"
                       @select="handleSelect">
        <template slot-scope="{ item }">
          <div class="search-item">
            <span class="name">{{ item.title }}</span>
            <br>
            <span class="actors">
              <span v-for="(actor,index) in item.casts"
                    :key="index">
                {{actor.name}}
              </span>
            </span>
          </div>
        </template>
      </el-autocomplete>
    </el-form-item>
    <el-form-item label="下载类型"
                  prop="type">
      <el-radio v-model="form.type"
                label="marget">磁力链接</el-radio>
      <el-radio v-model="form.type"
                label="thunder">迅雷链接</el-radio>
      <el-radio v-model="form.type"
                label="torrent">BT种子</el-radio>
    </el-form-item>
    <el-form-item label="下载链接"
                  prop="downloadUrl">
      <el-input v-model="form.downloadUrl"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary"
                 @click="submitForm('form')">立即创建</el-button>
      <el-button @click="resetForm('form')">重置</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import jsonp from 'jsonp'
export default {
  data () {
    return {
      movies: [],
      form: {
        name: '',
        movie: {},
        downloadUrl: '',
        type: 'marget'
      },
      rules: {
        name: [{ required: true, message: '请选择电影', trigger: 'blur' }],
        downloadUrl: [{ type: 'string', required: true }],
        type: [{ type: 'string', required: true }]
      }
    };
  },
  computed: {
    hasMovie () {
      return Object.keys(this.form.movie).length
    }
  },
  methods: {
    querySearchAsync (qs, callback) {
      jsonp('http://api.douban.com/v2/movie/search?q=' + qs, null, (err, res) => {
        if (err) {
          console.error(err.message);
        } else {
          this.movies = res.subjects
          callback(this.movies)
        }
      })
    },
    handleSelect (item) {
      console.log(item)
      this.form.movie = item
      this.form.name = item.title
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.axios.addMovie({
            id: this.form.movie.id,
            data: JSON.stringify(this.form.movie),
            downloadUrl: this.form.downloadUrl,
            type: this.form.type
          })
            .then(res => {
              console.log(res)
              if (res.data.addState) {
                this.$message({
                  message: '新增成功',
                  type: 'success'
                })
                this.$router.back()
              } else {
                this.$message.error('新增失败，请重试！')
                this.resetForm()
              }
            })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm (formName) {
      this.$refs[formName].resetFields();
      this.form.name = ''
    }
  }
}
</script>

<style scoped lang="scss">
li {
  line-height: normal;
}
.form {
  height: 100%;
  background: #fff;
  padding: 30px;
  border-radius: 10px;
}
.search-item {
  padding: 5px 0;
  img {
    width: 30px;
    height: 60px;
  }
  .name {
    color: rgb(47, 120, 255);
    font-size: 16px;
    height: 20px;
  }
  .actors {
    color: gray;
    font-size: 12px;
  }
}
.movie-card {
  width: 200px;
  margin: 0 auto;
  img {
    width: 100%;
  }
  .title {
    font-size: 16px;
    color: rgb(47, 120, 255);
  }
  .actors {
    color: gray;
    font-size: 12px;
  }
}
</style>



